<template>
  <div class="app-container">
    <el-row class="clearfix">
      <!-- <div class="fl">
        <el-button @click="handleGoBack">返回</el-button>
      </div> -->
      <div class="fr">
        <el-button type="primary" :disabled="!tableSelection.length" @click="handleNextStep('form')">下一步</el-button>
      </div>
    </el-row>
    <el-form ref="form" inline :model="form" :rules="rules" label-width="80px" label-position="left">
      <el-row>
        <el-col :span="6">
          <el-form-item label="退库单号" prop="billNumber">
            <el-input v-model="form.billNumber" disabled placeholder="请填写退库单号" style="width: 178px;" />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="退库日期" prop="date">
            <el-input v-model="form.date" disabled placeholder="请填写退库日期" style="width: 178px;" />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="出库单号" prop="stockOutNumber">
            <el-input v-model="form.stockOutNumber" disabled placeholder="请选择出库单" style="width: 143px;" />
            <el-button type="success" style="margin-left:5px;padding-left:0;padding-right:0;width:30px" icon="el-icon-plus" @click="stockReturnVisible = true" />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="是否检验" prop="inspect">
            <el-radio-group v-model="form.inspect" style="width: 178px;" @input="inspectChange">
              <el-radio-button :label="true">是</el-radio-button>
              <el-radio-button :label="false">否</el-radio-button>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-row>
      <el-form ref="queryForm" :model="query" inline class="clearfix top-filter" @submit.native.prevent>
        <el-form-item prop="materialCodeOrName" class="fl">
          <el-input v-model="query.materialCodeOrName" placeholder="物料编号或名称" style="width: 178px;" class="filter-item" @keyup.enter.native="searchInit" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="init"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('queryForm')"
        >重置</el-button>
        <el-button v-if="!isCheckAll" class="filter-item" type="warning" plain @click="checkAll">跨页全选</el-button>
        <el-button v-if="isCheckAll" class="filter-item" type="warning" plain @click="reverseSelection">跨页反选</el-button>
      </el-form>
    </el-row>
    <el-row>
      <table-com
        :ref="refName"
        :table-height-type="560"
        :loading="loading"
        :index="true"
        :index-fixed="false"
        :selection="true"
        :row-key="rowKey"
        :multiple="true"
        :selection-fixed="false"
        :table-column-sort="false"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="false"
        :operate-fixed="false"
        :operation-width="120"
        :table-selection="tableSelection"
        :selectable="selectable"
        @selectionChange="selectionChange"
        @refresh="searchInit"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <!-- 出库单选择列表 -->
    <return-warehouse-order-dialog
      v-model="stockReturnVisible"
      :selection="[]"
      :multiple="false"
      :extra-info="{forBusiness: 'returnWarehouse', excludeStockOutNumbers: form.stockOutNumber ? [form.stockOutNumber] : []}"
      @confirm="onSelectConfirm"
    />
  </div>
</template>
<script>
import ReturnWarehouseOrderDialog from '@/components/ModalSelect/ReturnWarehouseOrder'
import paginationPage from '@/mixins/paginationPage'
import Pagination from '@/components/Pagination'
import { getDetail as getListApi } from '@/apis/modules/out-bound/outbound-information' // 出库单详情
import { getAddApi, addStockReturnNextStepApi, getEditApi as editApi } from '@/apis/modules/out-bound/stock-return'

export default {
  // name: 'WmsOutBoundAddStockReturnInfo',
  name: 'OutBoundStockReturn',
  components: {
    Pagination,
    ReturnWarehouseOrderDialog
  },
  mixins: [paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        materialCodeOrName: ''
      },
      loading: false,
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null

        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'realNum',
          label: '出库数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'returnNum',
          label: '已退库数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      form: {
        billNumber: '', // 退库单号
        date: '', // 退库日期
        stockOutNumber: '', // 出库单号
        inspect: null // 是否检验
      },
      rules: {
        stockOutNumber: [
          { required: true, message: '请选择出库单号', trigger: 'blur' }
        ],
        date: [
          { required: true, message: '请填写退库日期', trigger: 'blur' }
        ],
        billNumber: [
          { required: true, message: '请填写退库单号', trigger: 'blur' }
        ],
        inspect: [
          { required: true, message: '请选择是否检验', trigger: 'change' }
        ]
      },
      stockReturnVisible: false,
      tableSelection: [],
      tkInfo: {}, // 退库信息
      rowKey: 'id',
      refName: 'baseTableRef',
      // 跨页全选标记
      isCheckAll: false
    }
  },
  computed: {
  },
  mounted() {
    this.generateStockReturnOrder()
  },
  methods: {
    // 生成退库单
    generateStockReturnOrder() {
      return new Promise((resolve, reject) => {
        getAddApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              // 赋值退库信息
              this.tkInfo = { ...data }
              // 赋值退库单号
              this.form.billNumber = this.tkInfo.billNumber
              // 赋值退库日期
              this.form.date = this.tkInfo.date
              // 赋值是否检验
              this.form.inspect = this.tkInfo.inspect
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 获取出库单物料
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        const params = {
          page: {
            ...this.pageInfo
          },
          ...this.query,
          stockOutNumber: this.form.stockOutNumber,
          forBusiness: 'returnWarehouse'
        }
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
            try {
              const rowKey = this.rowKey
              const ids = this.tableSelection.map(i => i[rowKey])
              this.tableData.forEach(row => {
                function check(row) {
                  const id = row[rowKey]
                  return ids.some(i => i === id)
                }
                if (check(row)) {
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })
            } catch (error) {
              console.log('捕获错误：', error)
            }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 返回
    handleGoBack() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({ name: 'OutBoundStockReturn' })
    },
    // 下一步
    handleNextStep(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) return
        addStockReturnNextStepApi({
          billNumber: this.form.billNumber,
          stockOutNumber: this.form.stockOutNumber,
          detailList: this.tableSelection.map(c => ({ stockOutDetailId: c.id })),
          allSelectedFlag: this.isCheckAll
        })
          .then(res => {
            this.$store.dispatch('tagsView/delView', this.$route)
            this.$router.push({
              name: 'WmsOutBoundStockReturnOrderInfo',
              params: {
                billNumber: this.tkInfo.billNumber
              }
            })
          })
          .catch(err => {
            console.log('err: ', err)
          })
      })
    },
    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 跨页全选
    checkAll() {
      this.isCheckAll = true
      const params = {
        ...this.query,
        stockOutNumber: this.form.stockOutNumber,
        forBusiness: 'returnWarehouse',
        page: {
          current: 1, // 当前页码
          size: 10000 // 每页条数
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list } = data
            if (list && Array.isArray(list) && list.length) {
              // 出库数量realNum 已退库数量returnNum
              this.tableSelection = list.filter(row => row.realNum - row.returnNum > 0)
              if (!this.tableSelection.length) {
                this.isCheckAll = false
                this.$notify.warning({
                  title: '警告',
                  message: `暂不支持跨页全选`
                })
                return
              }

              // 初始化当前页面的勾选
              const rowKey = this.rowKey
              const ids = this.tableSelection.map(i => i[rowKey]) // 满足可被勾选条件的数据
              const curPageCheckedArr = [] // 当前页满足可被勾选条件de项
              this.tableData.forEach(row => {
                function check(row) {
                  const id = row[rowKey]
                  return ids.some(i => i === id)
                }
                if (check(row)) {
                  curPageCheckedArr.push(row)
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })

              // 除了当前页的其余页勾选
              this.tableSelection.forEach(row => {
              // 排除当前页已勾选的项,并选中其它可勾选项
                const ids = curPageCheckedArr.map(i => i[rowKey])
                if (!ids.includes(row[rowKey])) {
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 跨页反选
    reverseSelection() {
      this.isCheckAll = false
      this.$nextTick(() => {
        this.$refs[this.refName].$refs.multipleTable.clearSelection()
        this.tableSelection = []
      })
    },
    // 选择出库单
    onSelectConfirm([item]) {
      const { stockOutNumber } = item
      this.form.stockOutNumber = stockOutNumber // 出库单号
      // 根据出库单号获取出库单的物料
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 是否检验状态改变回调
    inspectChange() {
      return new Promise((resolve, reject) => {
        editApi({ id: this.tkInfo.id, inspect: this.form.inspect })
          .then(res => {
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 控制表格某行是否可被勾选
    selectable(row, index) {
      // 出库数量realNum 已退库数量returnNum
      return row.realNum - row.returnNum > 0
    }
  }
}
</script>

<style scoped lang="scss">
</style>
